import React from "react"
import { Modal as DraggableModal } from "react-draggable-modal"
import SciFiButton from "../Button";
import 'react-draggable-modal/lib/modal.css';
import "./index.css"

const Alert = ({ open, onClose, onConfirm }) => {

    const handleConfirm = () => {
        onConfirm()
        onClose()
    }

    return (
        <DraggableModal visible={open} width="500px" height="100px" mask={false} onOk={onClose} onCancel={onClose} >
            <div className="flex flex-col justify-center items-center">
                <h1 className="flex">Allow sound on the website?</h1>
                <div className="flex justify-center items-center gap-1">
                    <SciFiButton onClick={handleConfirm}>Allow</SciFiButton>
                    <SciFiButton onClick={onClose}>Cancel</SciFiButton>
                </div>
            </div>
        </DraggableModal>
    )
}

export default Alert